html {
  color: $gray;
  font-size: 18px;
  font-family: $font-family-sans-serif;
}

.uppercase {
  text-transform: uppercase;
}

h1 {
  color: $aqua;
  font-family: $font-family-serif;
  font-size: 60px;
  font-weight: 700;
}

h2 {
  color: $green-med;
  font-family: $font-family-sans-serif;
  font-size: 46px;
  font-weight: 700;
}

h3 {
  color: $navy;
  font-family: $font-family-serif;
  font-size: 30px;
  font-weight: 700;
}

h4 {
  color: $navy;
  font-family: $font-family-sans-serif;
  font-size: 18px;
  font-weight: 700;
}

h5 {
  color: $gray;
  font-family: $font-family-serif;
  font-size: 18px;
  font-weight: 700;
}

h6 {
  color: $gray;
  font-family: $font-family-sans-serif;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
}

.hentry {
  ul {
    list-style-type: disc;
    padding-left: 2em;
  }

  li {
    list-style-type: inherit !important;
  }
}



